@import"https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;600;700&display=swap";*{margin:0;padding:0;box-sizing:border-box;font-family:"Nunito",sans-serif}body{background:linear-gradient(180deg, rgba(24, 171, 235, 0) 0%, rgba(24, 171, 235, 0.1) 100%),#fff;width:100vw;overflow-x:hidden;color:#374449}.App{padding:clamp(32px,2.5vw,48px) clamp(24px,10vw,144px);min-height:100vh}.App-header{display:flex;justify-content:space-between}.App main{display:flex;gap:20px;flex-wrap:wrap}.searchbar{display:flex;align-items:center;height:48px;border-radius:24px;width:fit-content;box-shadow:0px 4px 12px rgba(21,144,219,.25);background-color:#fff}.searchbar img:nth-child(1){min-width:22px;aspect-ratio:1/1;margin:0 13px}.searchbar input{height:48px;border:none;width:40vw}.searchbar input:focus-visible{outline:none}.searchbar button{display:flex;align-items:center;justify-content:center;background:linear-gradient(180deg, #18ABEB 0%, #1590DB 100%);width:48px;height:48px;border:none;border-radius:24px;cursor:pointer}.searchbar button img{width:24px;height:auto}.searchbar.focus{border:1px solid #b6deff}.searchbar.focus input{border-top:1px solid #b6deff;border-bottom:1px solid #b6deff}.unitSwitch{position:relative;display:inline-block;width:88px;height:48px;background-color:#fff;box-shadow:0px 4px 12px rgba(21,144,219,.25);border-radius:24px}.unitSwitch input{opacity:0;width:0;height:0}.unitSwitch .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;-webkit-transition:.4s;transition:.4s}.unitSwitch .slider:before{position:absolute;content:"°C";height:40px;width:40px;left:4px;bottom:4px;background:linear-gradient(180deg, #18ABEB 0%, #1590DB 100%);color:#fff;display:flex;justify-content:center;align-items:center;-webkit-transition:.4s;transition:.4s}.unitSwitch input:checked+.slider{background-color:#2196f3}.unitSwitch input:checked+.slider:before{-webkit-transform:translateX(40px);-ms-transform:translateX(40px);transform:translateX(40px);background:#fff;color:#2196f3;content:"°F"}.unitSwitch .slider.round{border-radius:34px}.unitSwitch .slider.round:before{border-radius:50%}.current{display:flex;justify-content:space-between;flex:2;margin-top:clamp(32px,2.5vw,48px);padding:clamp(24px,2.5vw,48px);background-color:#fff;box-shadow:0px 4px 12px rgba(21,144,219,.25);border-radius:clamp(24px,2.5vw,48px)}.current-main h1{font-weight:700;font-size:clamp(32px,2.08vw,40px)}.current-main h2{font-weight:600;font-size:clamp(20px,1.25vw,24px)}.current-main h3{font-weight:400;font-size:clamp(14px,.83vw,16px);margin-top:.75em}.current-main img{width:clamp(96px,6.66vw,128px);height:auto;margin-top:1em}.current-main p{font-weight:600;font-size:clamp(16px,1.25vw,24px)}.current-detail{text-align:right;display:flex;flex-direction:column;justify-content:space-between}.current-detail .description{color:#697377;font-size:clamp(12px,1.04vw,20px);font-weight:600}.current-detail .data,.current-detail .temperature{font-weight:700;font-size:clamp(22px,2.08vw,40px);margin-bottom:clamp(24px,2.08vw,40px)}.current-detail .data:last-child{margin-bottom:0}.current-detail .temperature{font-size:clamp(32px,3.33vw,64px)}.forecasts{flex:1;margin-top:clamp(24px,2.5vw,48px)}.forecasts .forecast{display:flex;justify-content:space-between;gap:clamp(12px,1.66vw,32px);margin-bottom:20px;padding:clamp(24px,2.5vw,48px);background-color:#fff;box-shadow:0px 4px 12px rgba(21,144,219,.25);border-radius:clamp(24px,2.5vw,48px)}.forecasts .forecast-main{flex:1}.forecasts .forecast-main h3{font-weight:700;font-size:20px;margin-bottom:1em}.forecasts .forecast-main .info{display:flex;justify-content:space-between}.forecasts .forecast-main .info .description{color:#697377;font-size:14px;font-weight:600}.forecasts .forecast-main .info .temperature,.forecasts .forecast-main .info .data{font-weight:700;font-size:clamp(24px,2.08vw,40px);margin-top:.25em}.forecasts .forecast-icon img,.forecasts .forecast-icon p{width:clamp(64px,4.79vw,92px);height:auto;text-align:center;font-size:12px;font-weight:600}.forecasts .forecast:last-child{margin-bottom:0}@media only screen and (max-width: 1440px){.current{flex:1.5}}@media only screen and (max-width: 1200px){.App main{gap:0}.App main .current{flex:auto;width:100%}.App main .current-main{flex:3}.App main .current-detail{flex:1;gap:16px}.App main .forecasts .forecast-main .info{justify-content:left;gap:32px}}@media only screen and (max-width: 576px){.App{padding-left:24px;padding-right:24px}.App .App-header{flex-wrap:wrap}.App .App-header .searchbar{width:100%}.App .App-header .searchbar input{width:100%}.App .App-header .unitSwitch{display:block;margin-top:24px;margin-left:auto;margin-right:0}.App main .current{flex-wrap:wrap}.App main .current-main{flex:auto;width:100%}.App main .current-detail{flex-direction:row;flex-wrap:wrap}.App main .current-detail>div{width:46%}.App main .current-detail .temperature-div{width:100%;text-align:left}.App main .current-detail .temperature-div .temperature{margin-bottom:0}.App main .current-detail .left{text-align:left}.App main .forecasts{flex:auto;width:100%}.App main .forecasts .forecast-main .info{gap:16px}}